import {
  Component, OnInit, ElementRef, ViewChildren, ViewChild,
  QueryList, HostListener, AfterViewInit, EventEmitter, OnDestroy
} from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';
import { map, debounceTime } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import { Department } from 'src/app/services/departments/department';
import { Employee } from 'src/app/services/employees/employee';
import { IListItemsPage } from 'src/app/services/list-items-service';
import { EmployeesService } from 'src/app/services/employees/employees.service';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { PortalService } from 'src/app/services/portal/portal.service';

@Component({
  selector: 'app-phonebook-page',
  templateUrl: './phonebook-page.component.html',
  styleUrls: ['./phonebook-page.component.scss']
})
export class PhonebookPageComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChildren('employeeCardElement', { read: ElementRef })
  employeeCardElements: QueryList<ElementRef>;

  @ViewChild('searchRef') searchRef: ElementRef;

  departments: Department[];
  department: Department = null;
  myDepartmentId: number = null;
  page: IListItemsPage<Employee>;
  hasNext = false;
  employees: Employee[] = [];
  userId: number = null;
  currentEmployee: Employee = null;
  subscription: Subscription = null;
  filter: string = null;
  isFavorite = false;

  constructor(
    private employeesService: EmployeesService,
    private route: ActivatedRoute,
    private sidebarService: SidebarService,
    private sharepointService: PortalService
  ) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
    this.userId = this.sharepointService.getCurrentUserContext().userId;
  }

  ngAfterViewInit(): void {
    this.subscription = fromEvent(this.searchRef.nativeElement, 'keyup')
      .pipe(
        map((e: any) => e.target.value),
        debounceTime(1000)
      ).subscribe((filter: string) => {
        this.filter = filter;
        this.searchEmployee();
      });
  }

  ngOnInit() {
    this.userId = this.route.snapshot.data.userId;
    this.departments = this.route.snapshot.data.departments;
    this.currentEmployee = this.route.snapshot.data.currentEmployee;
    this.createPage(this.route.snapshot.data.employees);
  }

  ngOnDestroy(): void {
    if (this.subscription != null) {
      this.subscription.unsubscribe();
    }
  }

  @HostListener('document:click', ['$event'])
  clickOutSide(el) {
    this.employeeCardElements.toArray().forEach(element => {
      if (!element.nativeElement.contains(el.target)) {
        element.nativeElement.className = 'employee';
      }
    });
  }

  toggleCard(employeeCardElement: HTMLElement) {
    const el = employeeCardElement;
    this.employeeCardElements.toArray().forEach(element => {
      element.nativeElement.className = 'employee';
    });
    !el.classList.contains('is-expanded')
      ? (el.className += ' is-expanded')
      : (el.className = 'employee');
  }

  public getAllEmployees(event?): void {
    this.myDepartmentId = null;
    this.isFavorite = false;
    this.searchEmployee();
  }

  public getLikedEmployees(event?): void {
    this.myDepartmentId = null;
    this.isFavorite = true;
    this.searchEmployee();
  }

  public getMyEmployees(event?): void {
    this.myDepartmentId = this.currentEmployee.lookupDepartmentId;
    this.isFavorite = false;
    this.searchEmployee();
  }

  public getEmergencyEmployees(event?): void {
    this.searchRef.nativeElement.value = null;
    this.employeesService
      .getItemsPaged({ filter: `slIsEmergency eq 1` })
      .subscribe(page => this.createPage(page));
  }

  public getEmployeesByDepartment(department): void {
    this.department = department;
    this.searchEmployee();
  }

  public toggleLike(employee: Employee): void {
    if (this.isLikedEmployee(employee)) {
      this.employeesService.removeLike({id: employee.id});
      employee.likedByIds = employee.likedByIds.filter(l => l !== this.userId);
    } else {
      this.employeesService.addLike({id: employee.id});
      employee.likedByIds.push(this.userId);
    }
  }

  public isLikedEmployee(employee: Employee): boolean {
    const isLiked = employee.likedByIds && employee.likedByIds.indexOf(this.userId) >= 0 || false;
    return isLiked;
  }

  public getNextEmployees(): void {
    this.page.getNext().subscribe(page => this.addPage(page));
  }

  public searchEmployee(): void {
    const value = this.filter;
    let filter: string = null;
    if (this.department != null) {
      filter = `substringof(' ${
        this.department.id
        } ',slStructureIds)`;
    }
    let employees = '';
    if (value && value !== '') {
      const keywords: string[] = value.split(' ');
      for (const keyword of keywords) {
        employees += `(substringof('${keyword}',slFullName) or substringof('${keyword}',slPositionName)) and `;
      }
      employees = employees.slice(0, -5);
    }
    if (filter == null) {
      if (employees !== '') {
        filter =  employees;
      }
    } else {
      if (employees !== '') {
        filter += ` and (${employees})`;
      }
    }
    if (this.myDepartmentId) {
      if (filter == null) {
        filter = '';
      } else {
        filter += ' and ';
      }
      filter += `substringof(' ${this.myDepartmentId} ',slStructureIds)`;
    }
    if (this.department) {
      if (filter == null) {
        filter = '';
      } else {
        filter += ' and ';
      }
      filter += `substringof(' ${this.department.id} ',slStructureIds)`;
    }
    if (this.isFavorite) {
      if (filter == null) {
        filter = '';
      } else {
        filter += ' and ';
      }
      filter += `LikedBy/Id eq ${this.userId}`;
    }
    this.employeesService
      .getItemsPaged({ filter: filter })
      .subscribe(page => this.createPage(page));
  }


  private createPage(page: IListItemsPage<Employee>): void {
    this.page = page;
    this.hasNext = page.hasNext;
    this.employees = page.items;
  }

  private addPage(page: IListItemsPage<Employee>): void {
    this.page = page;
    this.hasNext = page.hasNext;
    this.employees = this.employees.concat(page.items);
  }

  public getEmployeeLink(employee: Employee) {
    return `${window.location.protocol}//${window.location.host}`
    + `${this.sharepointService.getCurrentUserContext().webServerRelativeUrl}`
    + `/PortalPages/home.aspx#/phonebook/${employee.id}`;
  }
}
