import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PhonebookPageComponent } from './components/phonebook-page/phonebook-page.component';
import { ProfilePageComponent } from './components/profile-page/profile-page.component';

import { CurrentEmployeeResolver } from 'src/app/services/employees/current-employee.resolver';
import { CurrentUserIdResolver } from 'src/app/services/security/current-user-id.resolver';
import { EmployeesResolver } from 'src/app/services/employees/employees.resolver';
import { DepartmentsResolver } from 'src/app/services/departments/departments.resolver';
import { EmployeeResolver } from 'src/app/services/employees/employee.resolver';

const routes: Routes = [
  {
    path: '',
    component: PhonebookPageComponent,
    resolve: {
      employees: EmployeesResolver, departments: DepartmentsResolver,
      currentEmployee: CurrentEmployeeResolver, userId: CurrentUserIdResolver
    },
    data: { animation: 'fadeAnimation', title: 'Телефонный справочник' }
  },
  {
    path: ':id',
    component: ProfilePageComponent,
    resolve: { employee: EmployeeResolver, userId: CurrentUserIdResolver },
    data: { animation: 'fadeAnimation', title: 'Профиль пользователя' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [EmployeesResolver, DepartmentsResolver, CurrentEmployeeResolver, CurrentUserIdResolver, EmployeeResolver],
    exports: [RouterModule]
})
export class PhonebookRoutingModule { }
